.todos-container {
    margin-bottom: 20px;
    padding-top: 20px;
    color: white;    
    border-radius: 15px;    
  }

  .todo-wrapper {
    border-radius: 10px;
    margin-bottom: 10px;
    padding: 10px;
    background-color: #3c0d5d;
  }
  
  .todo-checkbox {
    margin: 0;
  }

  input[type="checkbox"]:checked + .edit-todo-input {
    text-decoration: line-through;
    text-decoration: none;
  }
  
  .btn-save {
    box-sizing: border-box;
    width: 5%;
    height: 34px;
    margin-left: 5px;
    border: 0;
    border-radius: 5px;
    background-color: #198754;    
    cursor: pointer;    
    font-size: 20px;
    padding: 4px;     
  }

  .btn-save:hover {
    opacity: 0.6;
    color: white;
    border: 1px solid white;
  }
  
  .btn-delete {
    box-sizing: border-box;
    width: 5%;
    height: 34px;
    margin-left: 5px;
    cursor: pointer;
    font-size: 20px;
    padding: 4px;    
    border: 0;
    border-radius: 5px;  
    color: black;
    background-color: #dc3545;     
  }
  
  .btn-delete:hover {
    opacity: 0.6;
    color: white;
    border: 1px solid white;
  }

  #checked {
    text-decoration: line-through;
    text-decoration: none;
  }
  
  .edit-todo-input {
    background-color: transparent;
    border: 0;
    color: white;
    width: 80%;
    margin-left: 10px;      
    font-size: 20px;
  }

  .edit-todo-input:focus {
    outline: 0;
  }  
  
  .todo-checkbox:checked {
    accent-color: #198754;    
  }

  .due-date-input {
    background-color: transparent;
    border: 0;
    color: white;    
  }